.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;

  background-color: #f4f6fa;
  box-shadow: -4px -4px 10px rgba(255, 255, 255, 0.8),
    -4px -4px 10px rgba(255, 255, 255, 0.625), 4px 4px 4px rgba(0, 0, 0, 0.15),
    inset 4px 4px 10px rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  cursor: pointer;
  border: none;
  background-position: center;
  background-repeat: no-repeat;
  &.settings {
    background-image: url(./images/settings.png);
  }
  &.edit {
    background-image: url(./images/edit.png);
  }
  &.delete {
    background-image: url(./images/delete.png);
  }
  &.home {
    background-image: url(./images/home.png);
  }
  &.close {
    background-image: url(./images/close.png);
  }
}

.button {
  &:active,
  &:focus-visible {
    box-shadow: inset -2px -2px 2px rgba(255, 255, 255, 0.8),
      inset -2px -2px 2px rgba(255, 255, 255, 0.625),
      inset 2px 2px 2px rgba(0, 0, 0, 0.15),
      inset 2px 2px 2px rgba(255, 255, 255, 0.1);
  }
  &.large:active,
  &.large:focus-visible {
    box-shadow: inset -3px -3px 16px rgba(255, 255, 255, 0.8),
      inset -3px -3px 6px rgba(255, 255, 255, 0.625),
      inset 3px 3px 3px rgba(0, 0, 0, 0.15),
      inset 3px 3px 6px rgba(255, 255, 255, 0.1);
  }
}

.small {
  width: 20px;
  height: 20px;
  background-size: 12px;
}
.medium {
  width: 40px;
  height: 40px;
  background-size: 25px;
  border-radius: 9px;
}
.large {
  width: 60px;
  height: 60px;
  background-size: 35px;
  border-radius: 13px;
}
.margin{
  margin-left: 5px;
}